import { Box, LinearProgress, Typography } from "@mui/material";
import { Document, Page, pdfjs } from "react-pdf";
import { Link } from "react-router-dom";
import ficha_tecnica from "../static/pdf/ficha_tecnica.pdf"
import axor from "../static/pdf/axor_sertificate_rosenheim.pdf"
import system_passport from "../static/pdf/system_pasport_ift_rosenheim_wds.pdf"
import 'react-pdf/dist/esm/Page/TextLayer.css';
import 'react-pdf/dist/esm/Page/AnnotationLayer.css';
import DownloadForOfflineIcon from '@mui/icons-material/DownloadForOffline';
import { useState } from "react";

const pdfs = [
  {
    doc:ficha_tecnica,
    title: "FICHA TCNICA | WDS 7 SRIES"
  },
  {
    doc:axor,
    title: "AXOR SERTIFICATE ROSENHEIM"
  },
  {
    doc:system_passport,
    title: "ift-System Passport Windows"
  }
]
function Certificates() {
  pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

  const [loading, setLoading] = useState("");
  const [page, setPage] = useState("none");
  
  let count = 0;
  const handleLoad = () => {
    count += 1;
    if(count == pdfs.length){
      setLoading("none");
      setPage("block");

    }
  };
  return (
    <><Box sx={{ width: '100%', display:loading,color:'#032e1e'}}>
      <LinearProgress color="inherit"/>
    </Box>
    <Box sx={{ marginTop:"20px", display:page}} component="section">
      
    <Typography variant="h3">Certificados</Typography>
    <Box sx={{display:"grid", gap:"12px", gridTemplateColumns:"repeat(auto-fill, minmax(350px,1fr))!important", marginTop:"12px"}}>
      {pdfs.map((item) => (
        <Box className="align-text" key={item.title}>
          <Typography className="pdf-title" >{item.title}</Typography>
          <Link to={item.doc} target="_blank">
            <Document file={item.doc} onLoadSuccess={handleLoad}>
                <Page wrap={false} pageNumber={1} />
            </Document>
          </Link>
          <Link to={item.doc} target="_blank" download className="download-document">
            <DownloadForOfflineIcon className="download-icon"/>
            <Box>Download</Box>
          </Link>
        </Box>
      ))}
    </Box>
    
    
    
  </Box>
  </>
  );
}

export default Certificates;