import * as React from 'react';
import { Container } from "@mui/system";
import logo from "../static/logo.png"
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import MenuIcon from '@mui/icons-material/Menu';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import {Link} from 'react-router-dom'
import HomeIcon from '@mui/icons-material/Home';
import ContactsIcon from '@mui/icons-material/Contacts';
import FolderIcon from '@mui/icons-material/Folder';
import CollectionsIcon from '@mui/icons-material/Collections';

const drawerWidth = 240;

const navItems=[
  {
    title: "Home",
    link:"/",
    icon: <HomeIcon />
  },
  {
    title: "Contactos",
    link:"/contacts",
    icon: <ContactsIcon />
  },
  {
    title: "Certificados",
    link:"/certificates",
    icon: <FolderIcon />
  },
  {
    title: "Galeria",
    link:"/gallery",
    icon: <CollectionsIcon />
  }
]

const navItemsSmall=[
  {
    title: "Home",
    link:"/",
    icon: <HomeIcon className='theme-color'/>
  },
  {
    title: "Contactos",
    link:"/contacts",
    icon: <ContactsIcon className='theme-color'/>
  },
  {
    title: "Certificados",
    link:"/certificates",
    icon: <FolderIcon className='theme-color'/>
  },
  {
    title: "Galeria",
    link:"/gallery",
    icon: <CollectionsIcon className='theme-color'/>
  }
]

function Header(props) {
  

  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };

  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: 'center' }}>
      <Typography variant="h6" 
      sx={{ my: 2,
        backgroundColor:"#032e1e",
        marginTop:"0",
        marginBottom:"0",
        paddingTop:"16px",
        paddingBottom:"16px"}}>
      <img src={logo} alt="Logo Janelion" width="150px"></img>
      </Typography>
      <Divider />
      <List>
        {navItemsSmall.map((item,key) => (
          <ListItem key={key} disablePadding>
            <ListItemButton  sx={{ textAlign: 'center' }}>
                <Link className='menu-link download-document' to={item.link} >
                  {item.icon}
                  <Box sx={{marginLeft:"5px"}} className='theme-color'>{item.title}</Box>
                  </Link>
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: 'flex' }} component="header">
      <CssBaseline />
      <AppBar component="nav" sx={{backgroundColor:"#032e1e"}}>
        <Container maxWidth='lg'>
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              sx={{ mr: 2, display: { sm: 'none' } }}
            >
              <MenuIcon  />
            </IconButton>
            <Typography
              variant="h6"
              component="div"
              sx={{ flexGrow: 1, display: {  sm: 'block' },paddingTop:"10px" }}
            >
              <Link to="/">
              <img src={logo} alt="Logo Janelion" width="250px"></img>
            </Link>
            </Typography>
            <Box sx={{ display: { xs: 'none', sm: 'block' } }}>
              {navItems.map((item,key) => (
                <Button key={key} sx={{ color: '#fff' }}>
                  <Link className='menu-link' to={item.link}>
                  {item.icon}
                    <Box>{item.title}</Box>
                  </Link>
                </Button>
              ))}
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
      <Box component="nav">
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
          }}
        >
          {drawer}
        </Drawer>
      </Box>
      
    </Box>
  );
}

export default Header;