function Gallery() {
  const [loading, setLoading] = useState("");
  const [page, setPage] = useState("none");
  
  let count = 0;
  const handleLoad = () => {
    count += 1;
    if(count == itemData.length){
      setLoading("none");
      setPage("block");
    }
  };
  return (
    <><Box sx={{ width: '100%', display:loading,color:'#032e1e'}}>
      <LinearProgress color="inherit"/>
    </Box>
    <Box sx={{ marginTop:"20px", display:page}} component="section">
    <Typography variant="h3">Galeria</Typography>
    <ImageList gap={12} sx={{gridTemplateColumns:"repeat(auto-fill, minmax(350px,1fr))!important"}}>
      {itemData.map((item) => (
        <ImageListItem key={item.img}>
          <img
            src={`${item.img}`}
            srcSet={`${item.img}`}
            alt={item.title}
            // loading="lazy"
            onLoad={() => handleLoad()}
          />
        </ImageListItem>
      ))}
    </ImageList>
  </Box>
  </>
  );
}

export default Gallery;